import React from "react";
import ScrollMenu from 'react-horizontal-scrolling-menu';
import styles from './Slider.module.css'

const list = [
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg', index:1 },
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg', index:2 },
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg', index:3 },
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg', index:4 },
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg' , index:5},
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg' , index:6},
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg' , index:7},
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg' , index:8},
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg' , index:9},
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg' , index:10},
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg' , index:11},
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg' , index:12},
    { name: 'https://www.codester.com/static/uploads/items/000/015/15306/preview-xl.jpg', index:13 }
  ];
  
  const MenuItem = ({text, selected}) => {
    return <div className={selected ? `${styles.menuItem} ${styles.active}`  : styles.menuItem}>
          <img src={text}/>
        </div>;
  };
  

  export const Menu = (list, selected) =>
 
    list.map(el => {
      const {name} = el;
      return <MenuItem text={el.name} key={el.index} selected={selected} />;
    });
  
  
  const Arrow = ({ text, className }) => {
    return (
      <div className={className}>{text}</div>
    );
  };
  
  
  const ArrowLeft = Arrow({ text: '<', className: styles.arrowPrev });
  const ArrowRight = Arrow({ text: '>', className: styles.arrowNext });
  
  const selected = 'item1';

class Slider extends React.Component {
    constructor(props) {
      super(props);
      // call it again if items count changes
      this.menuItems = Menu(props.items, selected);
    }
  
    state = {
      selected
    };
  
    onSelect = key => {
      this.setState({ selected: key });
      this.props.mainImageChanged(key)
    }
  
  
    render() {
      const { selected } = this.state;
      
      const menu = this.menuItems;
  
      return (
        <div>
          <ScrollMenu
            data={menu}
            arrowLeft={ArrowLeft}
            arrowRight={ArrowRight}
            selected={selected}
            onSelect={this.onSelect}

            scrollToSelected='true'
          />
        </div>
      );
    }
  }

  export default Slider