class ItemContainer extends React.Component {

    componentDidMount() {

    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.selectedSubChapterId != this.props.selectedSubChapterId) {
            this.props.setCurrentPage(1)
            if (this.props.selectedSubChapterId) {
                this.loadItems(this.props.selectedSubChapterId, 1)
            }
        }
    }

    itemDetail = (item) => {
        this.props.selectItem(item);
    }

    closeItem = () => {
        this.props.selectItem(null);
    }

    toBasket = (item) => {
        if (this.props.basketItems.filter(i => i.item.id == item.id) == 0)
            this.props.addItemToBasket(item)
        else alert("selected item already exist in the basket!")
    }

    loadItems = (subChapterId, pageNumber) => {
        let goods = items.filter(item => item.parentId == subChapterId)
        let selectedgoods = goods.slice((pageNumber - 1) * this.props.pageSize, pageNumber * this.props.pageSize)
        this.props.setTotalCount(goods.length)
        this.props.getItems(selectedgoods)
    }

    onPaging = (pageNumber) => {

        this.props.setCurrentPage(pageNumber)
        this.loadItems(this.props.selectedSubChapterId, pageNumber)
    }

    render() {
        let pageCount = Math.ceil(this.props.totalCount / this.props.pageSize);
        let pages = []
        for (var i = 1; i <= pageCount; i++)
            pages.push(i)

        return (

            <div className={styles.container}>

                {
                    this.props.selectedItem &&
                    <div className={this.props.selectedItem ? styles.overShow : styles.overHidden}>
                        <Item item={this.props.selectedItem} closeItem={this.closeItem} toBasket={this.toBasket} />
                    </div>
                }

                <div className={styles.itemList}>
                    {
                        this.props.items &&
                        this.props.items.map(item =>
                            <div className={styles.item}>
                                <div onClick={() => this.itemDetail(item)} >
                                    <div> <img src={item.itemImages.length>0 ? item.itemImages[0].name : ""} alt="" /></div>
                                    <div>{item.itemName}</div>
                                </div>

                                <div>
                                    <button onClick={() => this.toBasket(item)}>add</button>
                                </div>
                            </div>)
                    }

                </div>
                <div className={styles.pagingDiv}>
                    {
                        pages.map(index => {
                            if (index == 1 || index == pages[pages.length - 1] || index == this.props.currentPage || index == this.props.currentPage - 1 || index == this.props.currentPage + 1) {
                                if (index == this.props.currentPage - 1 && index > 2)
                                    return <span>
                                        <span>...</span>
                                        <span className={styles.paging} onClick={() => this.onPaging(index)}>{index}
                                        </span>
                                    </span>

                                else if (index == this.props.currentPage + 1 && index < pages[pages.length - 2])
                                    return <span>
                                        <span className={styles.paging} onClick={() => this.onPaging(index)}>{index}
                                        </span>
                                        <span>...</span>
                                    </span>

                                else if (index == this.props.currentPage)
                                    return <span className={styles.paging + " " + styles.current}>{index}</span>

                                else return <span className={styles.paging} onClick={() => this.onPaging(index)}>{index}</span>
                            }


                        })
                    }

                </div>
            </div >

        )

    }
}
const mapStateToProps = (state) => ({
    selectedSubChapterId: state.subChapters.selectedSubChapterId,
    items: state.items.items,
    selectedItem: state.items.selectedItem,
    basketItems: state.basket.basketItems,
    currentPage: state.items.currentPage,
    totalCount: state.items.totalCount,
    pageSize: state.items.pageSize
})

const mapDispatchToProps = (dispatch) => ({
    getItems: (items) => dispatch(GetItems_AC(items)),
    selectItem: (itemId) => dispatch(SelectItem_AC(itemId)),
    addItemToBasket: (item) => dispatch(ADD_ITEM_AC(item)),
    setTotalCount: (count) => dispatch(SetTotalCount_AC(count)),
    setCurrentPage: (pageNumber) => dispatch(SetCurrentPage_AC(pageNumber))
})

export default connect(mapStateToProps, mapDispatchToProps)(ItemContainer)