import React, { useState } from "react"
import close from '../../../Assets/close.png'
import styles from './Item.module.css'
import Slider from "./Slider/Slider";


const Item = (props) => {

    const [mainImage, steMainImage] = useState(props.item.itemImages[0].name);
    
    let changeImage=(key)=>{
        steMainImage(props.item.itemImages.filter(el=>el.index==key)[0].name)
    }
    
    return (
        <div>
            <div>
                <img src={close} alt="close" onClick={props.closeItem} className={styles.close} />
            </div>
            <div className={styles.content}>
                <div>
                    
                    {
                        props.item.itemImages.length>0 ?
                        <>
                            <img src={mainImage} />


                            <div className={styles.slideContainer}>

                            
                                <Slider items={props.item.itemImages} mainImageChanged={changeImage}/>

                            {/* {
                            props.item.itemImages.map(im=><div className={styles.slider} onClick={()=>changeImage(im)}><img src={im} /></div> )
                            } */}
                            </div>
                        </>
                        : ""
                    }
                    
                </div>
                

                <div>
                    {props.item.itemDescription.split('\n').map((item, i) => {
                        return <p key={i}>{item}</p>;
                    })}
                </div>
            </div>


            <div>
                <button onClick={() => props.toBasket(props.item)}>add</button>
            </div>
        </div>
    )
}

export default Item