const GET_SUB_CHAPTERS = "GET-SUB-CHAPTERS"
const SET_SELECTED_CHAPTER = "SET-SELECTED-CHAPTER"
const SET_SELECTED_SUBCHAPTER = "SET-SELECTED-SUBCHAPTER"

let initialState = {
    subChapters: null,
    selectedChapterId: null,
    selectedSubChapterId: null
}

export const subChapterReducer = (state = initialState, action) => {

    switch (action.type) {
        case GET_SUB_CHAPTERS:
            return { ...state, subChapters: action.subChapters }
        case SET_SELECTED_CHAPTER:
            return { ...state, selectedChapterId: action.chapterId }
        case SET_SELECTED_SUBCHAPTER:
            return { ...state, selectedSubChapterId: action.subChapterId }
        default:
            return state;
    }
}

export const GetSubChapters_AC = (subChapters) => ({ type: GET_SUB_CHAPTERS, subChapters })
export const SetChapterId_AC = (chapterId) => ({ type: SET_SELECTED_CHAPTER, chapterId })
export const SetSubChapterId_AC = (subChapterId) => ({ type: SET_SELECTED_SUBCHAPTER, subChapterId })