import React from "react"
import { BASKET_VISIBILITY_AC, EMPTY_BASKET_AC, UPDATE_BASKET_AC, REMOVE_ITEM_AC } from "../../../Store/basket-Reducer"
import { connect } from "react-redux"

class BasketDetails extends React.Component {

    closeBasket = () => {
        this.props.changeBasketState(false);
    }

    emptyBasket = () => {
        this.props.emptyBasket();
    }

    submitBasket = () => {
        alert("busket submitted");
        this.props.emptyBasket();
        this.props.changeBasketState(false);
    }

    changeQuantity = (e, id) => {
        this.props.changeQuantity(id, e.target.value)
    }

    removeItem = (id) => {
        this.props.removeItem(id)
    }

    render() {
        return (
            <div>
                Basket Items
                <div>
                    {
                        this.props.basketItems.map(bi =>
                            <div>
                                {bi.item.itemName}
                                <input value={bi.quantity} onChange={(e) => { this.changeQuantity(e, bi.item.id) }} />
                                <button onClick={() => { this.removeItem(bi.item.id) }}>remove</button>
                            </div>
                        )
                    }
                </div>
                <div>
                    <button onClick={this.closeBasket}>close</button>
                </div>
                <div>
                    <button onClick={this.emptyBasket}>empty</button>
                </div>
                <div>
                    <button onClick={this.submitBasket}>submit</button>
                </div>
            </div>

        )
    }

}

const mapStateToProps = (state) => ({
    basketItems: state.basket.basketItems
})

const mapDispatchToProps = (dispatch) => ({
    changeBasketState: (state) => dispatch(BASKET_VISIBILITY_AC(state)),
    emptyBasket: () => dispatch(EMPTY_BASKET_AC()),
    changeQuantity: (id, quantity) => dispatch(UPDATE_BASKET_AC(id, quantity)),
    removeItem: (id) => dispatch(REMOVE_ITEM_AC(id))
})

export default connect(mapStateToProps, mapDispatchToProps)(BasketDetails)