import React from 'react';
import styles from './App.module.css';
import HeaderContainer from './Components/Header/Header';
import ChapterContainer from './Components/Chapter/Chapter';
import MenuContainer from './Components/MainMenu/MainMenu';
import { Route } from 'react-router-dom';
import ContentContainer from './Components/Content/Content';
import ContactsContainer from './Components/Contact/Contacts';
import Footer from './Components/Footer/Footer';
import ItemContainer from './Components/Items/ItemContainer';

function App() {
  return (
    <div className={styles.appWraper}>
      <div className={styles.header}>
        <HeaderContainer />
      </div>
      <div className={styles.menu}>
        <MenuContainer />
      </div>
      <div className={styles.content}>

        <Route path='/chapter/:chapterId' render={() => <ChapterContainer />} />

        <Route path='/contacts' render={() => <ContactsContainer />} />

        <Route path='/' exact render={() => <ContentContainer />} />
      </div>
      <div className={styles.details}>

        <Route path='/chapter' render={() => <ItemContainer />} />

        <Route path='/' exact render={() => <ContentContainer />} />

        
      </div>
      <div className={styles.footer}>
        <Footer />
      </div>
    </div>
  );
}

export default App;
