import React from 'react'
import { Component } from 'react';
import { NavLink } from 'react-router-dom'
import { connect } from 'react-redux';
import { GetChapters_AC } from '../../Store/chapter-Reducer';
import styles from './MainMenu.module.css'
import { SelectItem_AC } from '../../Store/item-Reducer';

class MenuContainer extends Component {

    componentDidMount() {
        this.props.getChapters([
            { id: 1, chapterName: "Էլեկտրական գործիքներ"},
            { id: 2, chapterName: "Ձեռքի գործիքներ" },
            { id: 3, chapterName: "Այգեգործական" },
            { id: 4, chapterName: "Սանտեխնիկա" },
            { id: 5, chapterName: "Արտահագուստ" },
            { id: 6, chapterName: "Կրակմարիչներ" },
            { id: 7, chapterName: "Սարքավորումներ" }
        ])
    }

    render() {

        return (
            <div className={styles.navBar}>
                <div>
                    <NavLink to="/" activeClassName={styles.selected}>Home</NavLink>
                </div>
                {
                    this.props.chapters && 

                    this.props.chapters.map(ch => {
                        return <div key={ch.id} className={(this.props.selectedChapter===ch.id) ? styles.activeDiv : styles.deActiveDiv}>
                            <NavLink  to={`/chapter/${ch.id}`} activeClassName={styles.selected}>{ch.chapterName}</NavLink>
                        </div>
                    }) 
                }
                <div>
                    <NavLink to="/contacts" activeClassName={styles.selected}>Contacts</NavLink>
                </div>
            </div>
        )
    }
}

const mapStateToProps=(state)=>({
    chapters:state.chapters.chapters,
    selectedChapter: state.subChapters.selectedChapterId
})

const mapDispatchToProps=(dispatch)=>({
    getChapters:(chapters)=> dispatch(GetChapters_AC(chapters))
})


export default connect(mapStateToProps, mapDispatchToProps)(MenuContainer)