import React from 'react'
import { Component } from 'react';
import logo from '../../Assets/logo_dark.png'
import basket from '../../Assets/basket.png'
import styles from './Header.module.css'
import { connect } from 'react-redux';
import { BASKET_VISIBILITY_AC } from '../../Store/basket-Reducer';
import BasketDetails from './BasketDetails/BasketDetails';

class HeaderContainer extends Component {

    componentDidUpdate(prevProps, prevState) {
        if (this.props.itemsInBasket > prevProps.itemsInBasket)
            alert("item added!")
    }

    showBasket = () => {
        this.props.changeBasketState(true)
    }

    render() {
        return (
            <div className={styles.header}>
                <img src={logo} alt='logo' />
                <span className={styles.basket} onClick={this.showBasket}>

                    <img src={basket} alt='logo' />
                    <label>{this.props.itemsInBasket}</label>
                </span>
                {
                    this.props.basketState &&
                    <div className={styles.expandedBasket}>
                        <BasketDetails />
                    </div>
                }
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    itemsInBasket: state.basket.basketItems.length,
    basketState: state.basket.showBasket
})

const mapDispatchToProps = (dispatch) => ({
    changeBasketState: (state) => dispatch(BASKET_VISIBILITY_AC(state))
})

export default connect(mapStateToProps, mapDispatchToProps)(HeaderContainer)