import React from 'react'
import styles from './SubChapter.module.css'


const SubChapter = (props) => {

    return (
        <div>
            {props.subChapters.map(sCh => <div key={sCh.id} onClick={() => props.selectSuChapter(sCh.id)} className={styles.item}>{sCh.subChapterName}</div>)}
        </div>
    )
}

export default SubChapter