const GET_ITEMS = "GET-ITEMS"
const SET_SELECTED_ITEM = "SET-SELECTED-ITEM"
const SET_TOTAL_COUNT = "SET-TOTAL-COUNT"
const SET_CURRENT_PAGE = "SET-CURRENT-PAGE"


let initialState = {
    items: null,
    selectedItem: null,
    pageSize: 2,
    totalCount: 0,
    currentPage: 1
}

export const itemReducer = (state = initialState, action) => {

    switch (action.type) {
        case GET_ITEMS:
            return { ...state, items: action.items }
        case SET_SELECTED_ITEM:
            return { ...state, selectedItem: action.item }
        case SET_TOTAL_COUNT:
            return { ...state, totalCount: action.count }
        case SET_CURRENT_PAGE:
            return { ...state, currentPage: action.pageNumber }
        default:
            return state;
    }
}

export const GetItems_AC = (items) => ({ type: GET_ITEMS, items })
export const SelectItem_AC = (item) => ({ type: SET_SELECTED_ITEM, item })
export const SetTotalCount_AC = (count) => ({ type: SET_TOTAL_COUNT, count })
export const SetCurrentPage_AC = (pageNumber) => ({ type: SET_CURRENT_PAGE, pageNumber })
