const ADD_ITEM = "ADD-ITEM"
const REMOVE_ITEM = "REMOVE-ITEM"
const EMPTY_BASKET = "EMPTY-BASKET"
const UPDATE_BASKET = "UPDATE-BASKET"
const BASKET_VISIBILITY = "BASKET-VISIBILITY"

let initialState = {
    basketItems: [],
    showBasket: false
}

export const basketReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_ITEM:
            if (state.basketItems.filter(i => i.item.id == action.item.id) == 0)
                return { ...state, basketItems: [...state.basketItems, { item: action.item, quantity: 1 }] };
            return state;
        case REMOVE_ITEM:
            return { ...state, basketItems: state.basketItems.filter(el => el.item.id != action.id) };
        case EMPTY_BASKET:
            return { ...state, basketItems: [] };
        case UPDATE_BASKET:
            return { ...state, basketItems: state.basketItems.map((el) => { return el.item.id == action.id ? { ...el, quantity: action.quantity } : el }) }
        case BASKET_VISIBILITY:
            return { ...state, showBasket: action.state }
        default:
            return state;
    }
}

export const ADD_ITEM_AC = (item) => ({ type: ADD_ITEM, item })
export const REMOVE_ITEM_AC = (id) => ({ type: REMOVE_ITEM, id })
export const EMPTY_BASKET_AC = () => ({ type: EMPTY_BASKET })
export const UPDATE_BASKET_AC = (id, quantity) => ({ type: UPDATE_BASKET, id, quantity })
export const BASKET_VISIBILITY_AC = (state) => ({ type: BASKET_VISIBILITY, state: state })

