//const units=[{'կգ մետր լիտր հատ'}]


class ChapterContainer extends Component {

    componentDidMount() {
        this.LoadSubChapters(this.props.match.params.chapterId);
    }

    componentDidUpdate(prevProps, prevState) {
        this.props.closeSelectedItem()
        if (prevProps.selectedChapterID != this.props.match.params.chapterId) {
            this.LoadSubChapters(this.props.match.params.chapterId);
        }
    }

    componentWillUnmount(){
        this.props.closeSelectedItem()
    }

    LoadSubChapters = (chapterId) => {
        this.props.setSelectedChapter(chapterId)
        let sb = subChapters.filter(sCh => sCh.parentId == chapterId);
        this.props.getSubChapters(sb);

        sb.length>0 ? this.props.setSelectedSubChapter(sb[0].id) : this.props.setSelectedSubChapter(null)
    }

    OnSubChapterChanged = (subChapterId) => {
        this.props.setSelectedSubChapter(subChapterId)
        this.props.closeSelectedItem()
    }

    render() {

        return (
            <div className={styles.container}>
                <div className={styles.under}>
                    <div className={styles.subchapter}>
                        {
                            this.props.subChapters &&
                            <SubChapter subChapters={this.props.subChapters} selectSuChapter={this.OnSubChapterChanged} />
                        }
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    subChapters: state.subChapters.subChapters,
    selectedChapterID: state.subChapters.selectedChapterId
})

const mapDispatchToProps = (dispatch) => ({
    getSubChapters: (subChapters) => dispatch(GetSubChapters_AC(subChapters)),
    setSelectedChapter: (chapterId) => dispatch(SetChapterId_AC(chapterId)),
    setSelectedSubChapter:(subChapterId)=>dispatch(SetSubChapterId_AC(subChapterId)),
    closeSelectedItem:()=>dispatch(SelectItem_AC(null))
})

export default compose(withRouter, connect(mapStateToProps, mapDispatchToProps))(ChapterContainer) 