const GET_CHAPTERS = "GET-CHAPTERS"


let initialState = {
    chapters: null
}

export const chapterReducer = (state = initialState, action) => {

    switch (action.type) {
        case GET_CHAPTERS:
            return { ...state, chapters: action.chapters }
        
        default:
            return state;
    }
}

export const GetChapters_AC = (chapters) => ({ type: GET_CHAPTERS, chapters })
